.category {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  margin: 0 auto;

  background-color: @menu-index-color;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center -17px;
  background-size: 575px 373px;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-position: center -17px;
    background-size: 575px 373px;
  }

  @media (min-width: @tablet-width) {
    background-color: @category-bg-color;

    background-image: url("../img/hero-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center -45px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-repeat: no-repeat;
      background-position: center -45px;
      background-size: 1150px auto;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -10px;

      width: 100%;
      height: 24px;
      margin-top: -60px;

      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
      background-position: center top;
      background-size: 770px 24px;
    }
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/hero-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center -45px;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-repeat: no-repeat;
      background-size: 1150px auto;
      background-position: center -50px;
    }

    &::after {
      bottom: -1px;
      background-size: auto 34px;
      background-position: 0 top;
    }
  }
}

.category__header {
  margin: 0 auto;
  padding-top: 71px;
  padding-bottom: 124px;

  @media (min-width: @tablet-width) {
    padding-top: 110px;
    padding-bottom: 375px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 110px;
    padding-bottom: 361px;
  }
}

.category__title {
  width: 244px;
  margin: 0 auto;

  .font-bold();
  font-size: 27px;
  line-height: 1.11;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 500px;

    font-size: 43px;
    line-height: 1.16;
  }

  @media (min-width: @desktop-width) {
    width: 620px;

    font-size: 53px;
    line-height: 1.08;
  }
}

.category__list {
  .list-reset();
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

.category__link {
  display: block;
  padding-top: 26px;
  padding-bottom: 30px;
  padding-left: 29px;

  .font-bold();
  font-size: 19px;
  line-height: 1.26;
  color: @bg-color;

  transition: background-color 0.4s ease, color 0.1s ease, padding 0.4s ease, margin 0.4s ease;

  @media (min-width: @tablet-width) {
    padding-top: 27px;
    padding-left: 36px;

    font-size: 20px;
    line-height: 30px;
  }

  &:hover {
    padding-bottom: 35px;
    margin-top: -5px;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.category__span {
  display: block;
  width: 130px;
}

.category__item {
  width: 320px;

  @media (min-width: @tablet-width) {
    width: 334px;
    min-height: 119px;
  }

  @media (min-width: @desktop-width) {
    width: 525px;
    min-height: 125px;
  }

  &--interior {
    position: relative;
  }

  &--toys {
    position: relative;
  }
}

.category__link--interior {
  background-color: @interior-color;

  &:hover {
    background-color: #59d5bc;
  }
}

.category__link--toys {
  background-color: @toys-color;

  &:hover {
    background-color: #46c1ae;
  }
}

.category__item:hover .category__svg-interior {
  top: 18px;

  opacity: 0.7;

  @media (min-width: @tablet-width) {
    top: 25px;
  }
}

.category__item:active .category__svg-interior {
  top: 18px;

  opacity: 0.3;

  @media (min-width: @tablet-width) {
    top: 25px;
  }
}

.category__item:hover .category__svg-toys {
  top: 16px;

  opacity: 0.7;

  @media (min-width: @tablet-width) {
    top: 25px;
  }
}

.category__item:active .category__svg-toys {
  top: 16px;

  opacity: 0.3;

  @media (min-width: @tablet-width) {
    top: 25px;
  }
}

.category__svg-interior {
  position: absolute;
  top: 23px;
  right: 11.3%;

  width: 40px;
  height: 62.5px;

  fill: @svg-color;
  opacity: 0.5;
  cursor: pointer;

  transition: opacity 0.3s ease, top 0.3s ease;

  @media (min-width: @tablet-width) {
    top: 30px;
    right: 11%;
  }

  @media (min-width: @desktop-width) {
    right: 6.9%;
  }
}

.category__svg-toys {
  position: absolute;
  top: 21px;
  right: 8.5%;

  display: block;
  width: 60px;
  height: 59.6px;

  fill: @svg-color;
  opacity: 0.5;
  cursor: pointer;

  transition: opacity 0.3s ease, top 0.3s ease;

  @media (min-width: @tablet-width) {
    top: 30px;
    right: 11%;
  }

  @media (min-width: @desktop-width) {
    right: 6.7%;
  }
}
