.body {
  margin: 0 auto;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  letter-spacing: 0.15;
  color: #000000;
}
.body--index {
  background-image: linear-gradient(to bottom, #ffffff 1px, #ffffff 78px, #ececec 78px, #ececec 80px, #ffffff 80px, #ffffff 100px);
}
@media (min-width: 768px) {
  .body--index {
    background-image: linear-gradient(to bottom, #ffffff 1px, #ffffff 88px, #ececec 88px, #ececec 89px, #fbfbfb 89px, #fbfbfb 825px, #ffffff 825px);
  }
}
.body--inner-pages {
  background-image: linear-gradient(to bottom, #ffffff 1px, #ffffff 78px, #ececec 78px, #ececec 80px, #ffffff 79px, #ffffff 100px);
}
@media (min-width: 768px) {
  .body--inner-pages {
    background-image: linear-gradient(to bottom, #ffffff 1px, #ffffff 88px, #ececec 88px, #ececec 89px, #f1f1f1 89px, #f1f1f1 135px, #ffffff 135px);
  }
}
