}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.category {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center -17px;
  background-size: 575px 373px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .category {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-position: center -17px;
    background-size: 575px 373px;
  }
}
@media (min-width: 768px) {
  .category {
    background-color: #fbfbfb;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center -45px;
  }
  .category::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    width: 100%;
    height: 24px;
    margin-top: -60px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: center top;
    background-size: 770px 24px;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .category {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-repeat: no-repeat;
    background-position: center -45px;
    background-size: 1150px auto;
  }
}
@media (min-width: 1150px) {
  .category {
    background-image: url("../img/hero-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center -45px;
  }
  .category::after {
    bottom: -1px;
    background-size: auto 34px;
    background-position: 0 top;
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi), (min-width: 1150px) and (min-resolution: 1.5dppx) {
  .category {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-repeat: no-repeat;
    background-size: 1150px auto;
    background-position: center -50px;
  }
}
